<template>
    <div :style="menuContentSize">
      <div style="font-size: large;margin-bottom: 10px;">{{item.label}}</div>
      <v-select
          v-model="s_osmText"
          :items="tagOptions"
          label="11"
          item-value="value"
          item-title="label"
          @update:modelValue="change"
      ></v-select>

      <v-textarea
          v-model="s_rawQueryText"
          label=""
          outlined
          rows="3"
          placeholder="key=valueOSM"
      ></v-textarea>
      <v-btn style="margin-top: -10px" class="tiny-btn" @click="run"></v-btn>
      <v-btn style="margin-left: 5px;margin-top: -10px" class="tiny-btn" @click="reset"></v-btn>
      <v-btn style="margin-left: 5px;margin-top: -10px" class="tiny-btn" @click="clear"></v-btn>
      <v-btn style="margin-left: 5px;margin-top: -10px" class="tiny-btn" @click="prev">prev</v-btn>
      <v-btn style="margin-left: 5px;margin-top: -10px" class="tiny-btn" @click="next">next</v-btn>
      <v-btn style="margin-left: 5px;margin-top: -10px" class="tiny-btn" @click="delete0">del</v-btn><br>
      <v-btn style="margin-top: 0px" class="tiny-btn" @click="saveGeojson">geojson</v-btn>
      <v-btn style="margin-left: 5px;margin-top: 0px" class="tiny-btn" @click="saveCsv">csv</v-btn>

      <div v-html="item.attribution"></div>
    </div>
</template>

<script>
import osmtogeojson from 'osmtogeojson'
export default {
  name: 'ext-osm',
  props: ['mapName','item'],
  data: () => ({
    geojsonText: '',
    counter: 0,
    query: null,
    queryText: null,
    flg: false,
    selectedTag: null,
    tagOptions: [
      { key: '', value: '', label: '' },
      { key: 'highway', value: 'primary', label: '' },
      { key: 'highway', value: 'highway-all', label: '' },
      { key: 'leisure', value: 'park', label: '' },
      { key: 'amenity', value: 'school', label: '' },
      { key: 'amenity', value: 'hospital', label: '' },
      { key: 'natural', value: 'water', label: '' },
      { key: 'landuse', value: 'forest', label: '' },
      { key: 'highway', value: 'bus_stop', label: '' },
      { key: 'shop', value: 'supermarket', label: '' },
      { key: 'tourism', value: 'attraction', label: '' },
      { key: 'historic', value: 'historic-all', label: '' },
      { key: 'historic', value: 'wayside_shrine', label: '' },
      { key: 'historic', value: 'monument', label: '' },
      { key: 'historic', value: 'memorial', label: '' },
      { key: 'railway', value: 'station', label: '' },
      { key: 'amenity', value: 'restaurant', label: '' },
      { key: 'amenity', value: 'cafe', label: '' },
      { key: 'amenity', value: 'bank', label: '' },
      { key: 'amenity', value: 'library', label: '' },
      { key: 'amenity', value: 'post_office', label: '' },
      { key: 'leisure', value: 'playground', label: '' },
      { key: 'natural', value: 'beach', label: '' },
      { key: 'leisure', value: 'swimming_pool', label: '' },
      { key: 'building', value: 'apartments', label: '' },
      { key: 'aeroway', value: 'aerodrome', label: '' },
      { key: 'leisure', value: 'garden', label: '' },
      { key: 'amenity', value: 'parking', label: '' },
      { key: 'amenity', value: 'bicycle_parking', label: '' },
      { key: 'amenity', value: 'kindergarten', label: '' },
      { key: 'amenity', value: 'university', label: '' },
      { key: 'natural', value: 'peak', label: '' },
      { key: 'natural', value: 'spring', label: '' },
      { key: 'tourism', value: 'museum', label: '' },
      { key: 'tourism', value: 'zoo', label: '' },
      { key: 'tourism', value: 'aquarium', label: '' },
      { key: 'amenity', value: 'theatre', label: '' },
      { key: 'amenity', value: 'cinema', label: '' },
      { key: 'amenity', value: 'community_centre', label: '' },
      { key: 'leisure', value: 'sports_centre', label: '' },
      { key: 'natural', value: 'wetland', label: '' },
      { key: 'amenity', value: 'marketplace', label: '' },
      { key: 'amenity', value: 'fire_station', label: '' },
      { key: 'amenity', value: 'police', label: '' },
      { key: 'amenity', value: 'fountain', label: '' },
      { key: 'natural', value: 'cliff', label: '' },
      { key: 'natural', value: 'cave_entrance', label: '' },
      { key: 'amenity', value: 'bar', label: '' },
      { key: 'amenity', value: 'nightclub', label: '' },
      { key: 'shop', value: 'bakery', label: '' },
      { key: 'shop', value: 'butcher', label: '' },
      { key: 'shop', value: 'convenience', label: '' },
      { key: 'shop', value: 'clothes', label: '' },
      { key: 'shop', value: 'electronics', label: '' },
      { key: 'amenity', value: 'clinic', label: '' },
      { key: 'amenity', value: 'pharmacy', label: '' },
      { key: 'leisure', value: 'nature_reserve', label: '' },
      { key: 'tourism', value: 'viewpoint', label: '' },
      { key: 'tourism', value: 'theme_park', label: '' },
      { key: 'tourism', value: 'castle', label: '' },
      { key: 'waterway', value: 'river', label: '' },
      { key: 'waterway', value: 'stream', label: '' },
      { key: 'waterway', value: 'canal', label: '' },
      { key: 'amenity', value: 'fast_food', label: '' },

    ],
    menuContentSize: {'width':'330px','height': 'auto','margin': '10px', 'overflow': 'auto', 'user-select': 'text', 'font-size':'large'}
  }),
  computed: {
    s_extFire () {
      return this.$store.state.extFire
    },
    s_osmFire () {
      return this.$store.state.osmFire
    },
    s_osmText: {
      get() {
        return this.$store.state.osmText[this.mapName]
      },
      set(value) {
        this.$store.state.osmText[this.mapName] = value
      }
    },
    s_rawQueryText: {
      get() {
        return this.$store.state.rawQueryText[this.mapName]
      },
      set(value) {
        this.$store.state.rawQueryText[this.mapName] = value
      }
    },
  },
  methods: {
    update () {
      console.log(this.s_osmText)
      this.$store.commit('updateSelectedLayers',{mapName: this.mapName, id:this.item.id, values: [
          this.s_osmText,
          this.s_rawQueryText
        ]})
    },
    run () {
      if (this.s_rawQueryText){
        this.s_osmText = ''
        this.change ()

        let queryText = JSON.parse(localStorage.getItem('queryText'))
        if (!queryText) {
          queryText = []
        }
        queryText.push(this.s_rawQueryText)
        queryText = [...new Set(queryText)]
        localStorage.setItem('queryText',JSON.stringify(queryText))
        console.log(JSON.parse(localStorage.getItem('queryText')))

      }
    },
    reset () {
      const map = this.$store.state[this.mapName]
      map.getSource('osm-overpass-source').setData({
        type: 'FeatureCollection',
        features: [] //  features 
      });
      this.queryText = ''
    },
    clear () {
      const map = this.$store.state[this.mapName]
      map.getSource('osm-overpass-source').setData({
        type: 'FeatureCollection',
        features: [] //  features 
      });
      this.s_osmText = ''
      this.s_rawQueryText = ''
      this.queryText = ''
    },
    prev () {
      const queryText = JSON.parse(localStorage.getItem('queryText'));
      // 
      if (this.counter > 0) {
        this.counter--;
      }
      this.s_rawQueryText = queryText[this.counter];
    },
    next () {
      const queryText = JSON.parse(localStorage.getItem('queryText'));
      // 
      if (this.counter < queryText.length - 1) {
        this.counter++;
      }
      this.s_rawQueryText = queryText[this.counter];
    },
    delete0 () {
      let queryText = JSON.parse(localStorage.getItem('queryText'));
      queryText = queryText.filter(v => v !== this.s_rawQueryText)
      console.log(queryText)
      localStorage.setItem('queryText',JSON.stringify(queryText))
      this.s_rawQueryText = queryText[this.counter];
    },
    saveGeojson () {
      // GeoJSONBlob
      const blob = new Blob([this.geojsonText], { type: 'application/json' });

      // 
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = 'osm.geojson'; // 

      // 
      document.body.appendChild(link);
      link.click();

      // : 
      document.body.removeChild(link);
      URL.revokeObjectURL(link.href);
    },
    saveCsv () {
      // GeoJSONCSV
      function downloadGeoJSONAsCSV(geojsonText) {
        try {
          // GeoJSON
          const geojson = JSON.parse(geojsonText);

          // FeatureCollection
          if (!geojson.features || !Array.isArray(geojson.features)) {
            throw new Error('GeoJSONFeatureCollection');
          }

          // CSV
          const headers = new Set();
          geojson.features.forEach(feature => {
            if (feature.properties) {
              Object.keys(feature.properties).forEach(key => headers.add(key));
            }
          });
          headers.add('longitude'); // 
          headers.add('latitude'); // 

          const headerArray = Array.from(headers);
          let csv = headerArray.join(',') + '\n';

          // FeatureCSV
          geojson.features.forEach(feature => {
            const row = headerArray.map(header => {
              if (header === 'longitude' || header === 'latitude') {
                if (feature.geometry && feature.geometry.type === 'Point') {
                  return header === 'longitude'
                      ? feature.geometry.coordinates[0]
                      : feature.geometry.coordinates[1];
                }
                return '';
              } else {
                return feature.properties?.[header] || '';
              }
            });
            csv += row.join(',') + '\n';
          });

          // Blob
          const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
          const link = document.createElement('a');
          const url = URL.createObjectURL(blob);
          link.setAttribute('href', url);
          link.setAttribute('download', 'osm.csv');
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          URL.revokeObjectURL(url);

        } catch (error) {
          console.error('GeoJSONCSV:', error.message);
        }
      }
      downloadGeoJSONAsCSV(this.geojsonText)
    },
    change () {
      const vm = this
      const map = this.$store.state[this.mapName]

      if (this.s_rawQueryText) {

        if (this.s_rawQueryText.includes("user")) {

          const name = this.s_rawQueryText.match(/"([^"]+)"/)[1];
          console.log(name);

          async function getChangesetQuery(userName) {
            try {
              // 24ISO
              const now = new Date();
              const yesterday = new Date(now.getTime() - 72 * 60 * 60 * 1000);
              const timeFilter = `${yesterday.toISOString()},${now.toISOString()}`;

              // Changeset ID72
              const changesetUrl = `https://api.openstreetmap.org/api/0.6/changesets?display_name=${userName}&time=${timeFilter}`;
              const changesetResponse = await fetch(changesetUrl);
              const changesetData = await changesetResponse.text();

              // Changeset ID
              const changesetIds = [...changesetData.matchAll(/id="(\d+)"/g)]
                  .map(match => parseInt(match[1], 10))
                  .sort((a, b) => b - a)
                  .slice(0, 200); // 200

              if (changesetIds.length === 0) {
                throw new Error('24Changeset ID');
              }

              // 1Changeset
              const changesetId = changesetIds[0];
              const changesetDetailUrl = `https://api.openstreetmap.org/api/0.6/changeset/${changesetId}/download`;
              const detailResponse = await fetch(changesetDetailUrl);
              const detailData = await detailResponse.text();

              // NodeWayRelationIDID
              const nodeIds = [...detailData.matchAll(/<node id="(\d+)"/g)].map(match => match[1]);
              const wayIds = [...detailData.matchAll(/<way id="(\d+)"/g)].map(match => match[1]);
              const relationIds = [...detailData.matchAll(/<relation id="(\d+)"/g)].map(match => match[1]);

              // BBOX
              const bounds = map.getBounds();
              const bbox = `${bounds.getSouth()},${bounds.getWest()},${bounds.getNorth()},${bounds.getEast()}`;
              // console.log('BBOX:', bbox);

              // Overpass API (BBOX)
              const query = `[out:json][timeout:60];
             (
              ${nodeIds.map(id => `node(${id})(${bbox});`).join(' ')}
              ${wayIds.map(id => `way(${id})(${bbox});`).join(' ')}
              ${relationIds.map(id => `relation(${id})(${bbox});`).join(' ')}
             );
              (._;>;);
              out body;`;

              // console.log('Overpass API:', query);

              // Overpass APIMapLibre (POST)
              const url = `https://overpass.openstreetmap.jp/api/interpreter`;
              const response = await fetch(url, {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
                },
                body: `data=${encodeURIComponent(query)}`
              });
              const data = await response.json();
              const geojson = osmtogeojson(data);
              vm.geojsonText = JSON.stringify(geojson)
              map.getSource('osm-overpass-source').setData(geojson);
              map.getCanvas().style.cursor = 'default'
              console.log('MapLibreGeoJSON');
            } catch (error) {
              console.error(':', error.message);
              document.body.style.cursor = 'default';
              throw error;
            }
          }

          (async () => {
            try {
              await getChangesetQuery(name);
            } catch (error) {
              console.error(':', error);
            }
          })();

        } else {
          const bounds = map.getBounds();
          const bbox = `${bounds.getSouth()},${bounds.getWest()},${bounds.getNorth()},${bounds.getEast()}`;
          if (!this.s_rawQueryText.includes('\n')) {
            // 
            function splitKeyValue(input) {
              const match = input.match(/^(\w+)\s*[=,\s]\s*(\w+)$/);
              if (match) {
                return {key: match[1], value: match[2]};
              } else {
                return {key: input,value: null};
              }
            }
            const {key, value} = splitKeyValue(this.s_rawQueryText);
            console.log(key, value)
            function buildOverpassQuery(key, value) {
              let rawQueryText;
              if (value === null || value === undefined) {
                // valuenullundefinedkey
                rawQueryText = '[out:json][timeout:60];' +
                    '(' +
                    'node[' + key + '](' + bbox + ');' +
                    'way[' + key + '](' + bbox + ');' +
                    'relation[' + key + '](' + bbox + ');' +
                    ');' +
                    '(._;>;);' +
                    'out body qt;';
              } else {
                rawQueryText = '[out:json][timeout:60];' +
                    '(' +
                    'node[' + key + '=' + value + '](' + bbox + ');' +
                    'way[' + key + '=' + value + '](' + bbox + ');' +
                    'relation[' + key + '=' + value + '](' + bbox + ');' +
                    ');' +
                    '(._;>;);' +
                    'out body qt;';
              }
              return rawQueryText;
            }
            this.queryText = buildOverpassQuery(key, value);
            console.log(':', this.queryText);
          } else {
            this.queryText = this.query.replace(/^\s*\/\/.*$\n?/gm, '')
            this.queryText = this.queryText.replace(/\(\s*user:[^)]*\)\s*/g, '');
            this.queryText = this.queryText.replace(/\s*\n\s*/g, ' ');
            this.queryText = this.queryText.replace(/\{\{bbox\}\}/g, bbox);
            this.queryText = this.queryText.replace(
                /\bnwr\s*\((.*?)\)\s*\((.*?)\)\s*;?/g,
                'node($1)($2);\nway($1)($2);\nrelation($1)($2);'
            );
            this.queryText = this.queryText.replace(/\{\{date:(\d+)hours\}\}/g, (_, hours) => {
              const pastDate = new Date(Date.now() - Number(hours) * 60 * 60 * 1000).toISOString();
              return pastDate;
            });
          }
        }
        console.log(this.queryText)
      }

      //
      //
      //
      //
      //
      //
      //
      //
      //

      if (!this.s_osmText && !this.s_rawQueryText) {
        map.getSource('osm-overpass-source').setData({
          type: 'FeatureCollection',
          features: [] //  features 
        });
        this.update()
        return
      }

      if (map.getZoom() <= 11) {
        map.getSource('osm-overpass-source').setData({
          type: 'FeatureCollection',
          features: [] //  features 
        });
        return
      }

      async function fetchOverpassData(tagKey, tagValue) {
        // const bounds = map.getBounds();
        // const query = '[out:json];' +
        //     '(' +
        //     'node["amenity"="drinking_water"](' + bounds.getSouth() + ',' + bounds.getWest() + ',' + bounds.getNorth() + ',' + bounds.getEast() + '););' +
        //     'out body;';

        // node [amenity=drinking_water]({{bbox}});out;

        const bounds = map.getBounds();
        // const bbox = `${bounds.getSouth()},${bounds.getWest()},${bounds.getNorth()},${bounds.getEast()}`;

        // const query = vm.queryText
        // console.log(query)
        let query = tagValue.includes("highway-all")
            ? '[out:json];' +
            '(' +
            'way["' + tagKey + '"](' +
            bounds.getSouth() + ',' + bounds.getWest() + ',' + bounds.getNorth() + ',' + bounds.getEast() + ');' +
            ');' +
            'out body;' +
            '>;' +
            'out skel qt;'
            : tagValue.includes("-all")
                ? '[out:json];' +
                '(' +
                'way[' + tagKey + '](' +
                bounds.getSouth() + ',' + bounds.getWest() + ',' + bounds.getNorth() + ',' + bounds.getEast() + ');' +
                'node[' + tagKey + '](' +
                bounds.getSouth() + ',' + bounds.getWest() + ',' + bounds.getNorth() + ',' + bounds.getEast() + ');' +
                'relation[' + tagKey + '](' +
                bounds.getSouth() + ',' + bounds.getWest() + ',' + bounds.getNorth() + ',' + bounds.getEast() + ');' +
                ');' +
                'out body;' +
                '>;' +
                'out skel qt;'
                : '[out:json];' +
                '(' +
                'way["' + tagKey + '"="' + tagValue + '"](' +
                bounds.getSouth() + ',' + bounds.getWest() + ',' + bounds.getNorth() + ',' + bounds.getEast() + ');' +
                'node["' + tagKey + '"="' + tagValue + '"](' +
                bounds.getSouth() + ',' + bounds.getWest() + ',' + bounds.getNorth() + ',' + bounds.getEast() + ');' +
                ');' +
                'out body;' +
                '>;' +
                'out skel qt;';

        if (vm.s_rawQueryText) {
          query = vm.queryText
        }
        console.log(query)
        // alert(query)
        const url = `https://overpass.openstreetmap.jp/api/interpreter?data=${encodeURIComponent(query)}`;

        try {
          const response = await fetch(url);
          const data = await response.json();
          const geojson = osmtogeojson(data);
          return geojson;
        } catch (error) {
          console.error('Error fetching Overpass data:', error);
        }
      }

      // MapLibreGeoJSON
      async function addOverpassLayer(tagKey, tagValue) {
        map.getCanvas().style.cursor = 'wait'
        const geojsonData = await fetchOverpassData(tagKey, tagValue)
        if (geojsonData) {
          console.log(JSON.stringify(geojsonData))
          vm.geojsonText = JSON.stringify(geojsonData)
          if (!map.getSource('osm-overpass-source')) {
            map.addSource('osm-overpass-source', {
              type: 'geojson',
              data: geojsonData
            })
          } else {
            map.getSource('osm-overpass-source').setData(geojsonData)
          }
          map.getCanvas().style.cursor = 'default'
        }
      }

      console.log(this.s_osmText)
      function getKeyByValue(value) {
        const option = vm.tagOptions.find(option => option.value === value);
        return option ? option.key : null; //  null 
      }
      console.log(getKeyByValue(this.s_osmText))

      addOverpassLayer(getKeyByValue(this.s_osmText),this.s_osmText)

      this.update()
    },
  },
  mounted() {
    // const script = document.createElement('script');
    // script.src = "https://cdnjs.cloudflare.com/ajax/libs/osmtogeojson/3.0.0/osmtogeojson.min.js";
    // document.head.appendChild(script);
  },
  watch: {
    s_osmFire () {
      this.change()
    },
    s_extFire () {
      this.change()
    },
  }
}
</script>
